
import { defineComponent, watchEffect, reactive, toRefs } from 'vue'
import axios from 'axios'

import GameCard from '@/components/GridCards/GameCard.vue'
import SvgIcon from '@/components/SvgIcon.vue'

export default defineComponent({
  setup() {
    const state = reactive({
      isLoading: false,
      results: [] as any,
      currentPage: 1,
      ordering: '-added',
      dates: '1960-01-01,2022-01-01',
      toggleOrder: false
    })

    watchEffect(() => {
      state.isLoading = true
      state.results = []
      const fetchItems = async () => {
        const response = await axios(
          `https://api.rawg.io/api/games?key=c0233312e0954f0ca72e3d1e9d64ef32&dates=${state.dates}&ordering=${state.ordering}&page=${state.currentPage}&page_size=20`
        )
        state.results.push(response.data.results)
        state.isLoading = false
      }
      fetchItems()
    })

    function changeOrder(order) {
      state.currentPage = 1
      state.ordering = order
    }

    const getNextPage = () => {
      state.currentPage++
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    }
    const getPrevPage = () => {
      if (state.currentPage == 1) return
      state.currentPage--
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    }
    const getFirstPage = () => {
      state.currentPage = 1
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    }

    return {
      ...toRefs(state),
      getNextPage,
      getPrevPage,
      getFirstPage,
      changeOrder
    }
  },
  components: {
    GameCard,
    SvgIcon
  }
})
